  --gray-3: #dee2e6;
  /* Borders, dividers */
  --gray-4: #ced4da;
  --gray-5: #adb5bd;
  /* Disabled text, placeholders */
  --gray-6: #868e96;
  /* Muted text */
  --gray-7: #495057;
  /* Secondary text */
  --gray-8: #343a40;
  /* Primary text */
  --gray-9: #212529;
  /* Darkest - headings, dark mode backgrounds */

  /* --- PRIMARY: Indigo Scale (Open Color Indigo 0-9) ---
   * Global default for buttons, links, highlights, brand elements
   * Used for: Buttons, links, highlights, brand elements
   */
  --primary-0: #edf2ff;
  /* Lightest - hover backgrounds, highlights */
  --primary-1: #dbe4ff;
  --primary-2: #bac8ff;
  --primary-3: #91a7ff;
  --primary-4: #748ffc;
  --primary-5: #5c7cfa;
  /*  Main brand color */
  --primary-6: #4c6ef5;
  /* Hover state */
  --primary-7: #4263eb;
  /* Active state */
  --primary-8: #3b5bdb;
  --primary-9: #364fc7;
  /* Darkest - pressed state */

  /* --- SECONDARY: Teal Scale (Open Color Teal 0-9) ---
   * Complementary to Cyan for secondary actions
   */
  --secondary-0: #e6fcf5;
  --secondary-1: #c3fae8;
  --secondary-2: #96f2d7;
  --secondary-3: #63e6be;
  --secondary-4: #38d9a9;
  --secondary-5: #20c997;
  /*  Main secondary color */
  --secondary-6: #12b886;
  /* Hover state */
  --secondary-7: #0ca678;
  /* Active state */
  --secondary-8: #099268;
  --secondary-9: #087f5b;

  /* --- FUNCTIONAL: Semantic Colors ---
   * Used for: Status indicators, feedback, alerts
   */

