 * DYNAMIC RADIUS SWITCHING (Theme Customizer)
 * These rules remap --radius-fib-* variables based on [data-radius] attribute
 * Values: 0, 0.125, 0.25, 0.375, 0.5 (rem multiplier)
 *  */

/* No radius (sharp corners) */
[data-radius="0"] {
  --radius-fib-2: 0;
  --radius-fib-3: 0;
  --radius-fib-5: 0;
  --radius-fib-8: 0;
  --radius-fib-13: 0;
  --radius-fib-21: 0;
}

/* Extra small radius (0.125rem base) */
[data-radius="0.125"] {
  --radius-fib-2: 0.0625rem;   /* 1px */
  --radius-fib-3: 0.09375rem;  /* 1.5px */
  --radius-fib-5: 0.15625rem;  /* 2.5px */
  --radius-fib-8: 0.25rem;     /* 4px */
  --radius-fib-13: 0.40625rem; /* 6.5px */
  --radius-fib-21: 0.65625rem; /* 10.5px */
}

/* Small radius (0.25rem base - default) */
[data-radius="0.25"] {
  --radius-fib-2: 0.125rem;    /* 2px */
  --radius-fib-3: 0.1875rem;   /* 3px */
  --radius-fib-5: 0.3125rem;   /* 5px */
  --radius-fib-8: 0.5rem;      /* 8px */
  --radius-fib-13: 0.8125rem;  /* 13px */
  --radius-fib-21: 1.3125rem;  /* 21px */
}

/* Medium radius (0.375rem base) */
[data-radius="0.375"] {
  --radius-fib-2: 0.1875rem;   /* 3px */
  --radius-fib-3: 0.28125rem;  /* 4.5px */
  --radius-fib-5: 0.46875rem;  /* 7.5px */
  --radius-fib-8: 0.75rem;     /* 12px */
  --radius-fib-13: 1.21875rem; /* 19.5px */
  --radius-fib-21: 1.96875rem; /* 31.5px */
}

/* Large radius (0.5rem base) */
[data-radius="0.5"] {
  --radius-fib-2: 0.25rem;     /* 4px */
  --radius-fib-3: 0.375rem;    /* 6px */
  --radius-fib-5: 0.625rem;    /* 10px */
  --radius-fib-8: 1rem;        /* 16px */
  --radius-fib-13: 1.625rem;   /* 26px */
  --radius-fib-21: 2.625rem;   /* 42px */
}


/* Icons - All Phosphor Weights */
/**
 * Vanduo Icons - All Phosphor Weights
 * 
 * WARNING: This imports ~3MB of font assets. Only use if you need all weights.
 * For most projects, use icons.css (regular + fill) instead.
 * 
 * Available weights:
 *   <i class="ph ph-heart"></i>           - Regular
 *   <i class="ph-fill ph-heart"></i>      - Fill
 *   <i class="ph-bold ph-heart"></i>      - Bold
 *   <i class="ph-light ph-heart"></i>     - Light
 *   <i class="ph-thin ph-heart"></i>      - Thin
 *   <i class="ph-duotone ph-heart"></i>   - Duotone
 * 
 * Full icon list: https://phosphoricons.com
 */

@font-face {
  font-family: "Phosphor";
  src:
    url('./icons/phosphor/regular/Phosphor.woff2') format("woff2"),
    url('./icons/phosphor/regular/Phosphor.woff') format("woff"),
    url('./icons/phosphor/regular/Phosphor.ttf') format("truetype"),
    url('./icons/phosphor/regular/Phosphor.svg#Phosphor') format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

.ph {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "Phosphor" !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Enable Ligatures ================ */
  letter-spacing: 0;
  -webkit-font-feature-settings: "liga";
  -moz-font-feature-settings: "liga=1";
  -moz-font-feature-settings: "liga";
  -ms-font-feature-settings: "liga" 1;
  font-feature-settings: "liga";
  -webkit-font-variant-ligatures: discretionary-ligatures;
  font-variant-ligatures: discretionary-ligatures;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

