.translate-y-8 { transform: translateY(8px); }
.-translate-y-1 { transform: translateY(-1px); }
.-translate-y-2 { transform: translateY(-2px); }
.-translate-y-3 { transform: translateY(-3px); }
.-translate-y-5 { transform: translateY(-5px); }
.-translate-y-8 { transform: translateY(-8px); }

/* Translate percentage-based */
.translate-x-full { transform: translateX(100%); }
.-translate-x-full { transform: translateX(-100%); }
.translate-x-half { transform: translateX(50%); }
.-translate-x-half { transform: translateX(-50%); }
