/**
 * Vanduo Framework - Progress Bar Component
 * CSS-only progress bar with variants and sizes
 */

:root {
  --progress-height: 8px;
  --progress-bg: var(--bg-tertiary, #e9ecef);
  --progress-bar-bg: var(--color-primary);
  --progress-border-radius: var(--radius-fib-5, 5px);
}

/* Base Progress */
.vd-progress {
  display: flex;
  height: var(--progress-height);
  overflow: hidden;
  background-color: var(--progress-bg);
  border-radius: var(--progress-border-radius);
}

.vd-progress-bar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  color: #fff;
  text-align: center;
  white-space: nowrap;
  background-color: var(--progress-bar-bg);
  transition: width 0.4s ease;
  border-radius: var(--progress-border-radius);
}

/* Variants */
.vd-progress-bar.vd-progress-success { background-color: var(--color-success); }
.vd-progress-bar.vd-progress-warning { background-color: var(--color-warning); }
.vd-progress-bar.vd-progress-error { background-color: var(--color-error); }
.vd-progress-bar.vd-progress-info { background-color: var(--color-info); }

/* Sizes */
.vd-progress-xs { --progress-height: 3px; }
.vd-progress-sm { --progress-height: 5px; }
.vd-progress-lg { --progress-height: 13px; }
.vd-progress-xl { --progress-height: 21px; }

/* Labeled (show text) */
.vd-progress-xl .vd-progress-bar,
.vd-progress-lg .vd-progress-bar {
  font-size: var(--font-size-xs, 0.75rem);
  line-height: 1;
}

/* Striped */
.vd-progress-striped .vd-progress-bar {
  background-image: linear-gradient(
    45deg,
    rgba(255, 255, 255, 0.15) 25%,
    transparent 25%,
    transparent 50%,
    rgba(255, 255, 255, 0.15) 50%,
    rgba(255, 255, 255, 0.15) 75%,
    transparent 75%,
    transparent
  );
  background-size: 1rem 1rem;
}

/* Animated Striped */
.vd-progress-animated .vd-progress-bar {
  animation: progress-bar-stripes 1s linear infinite;
}

@keyframes progress-bar-stripes {
  from { background-position: 1rem 0; }
  to { background-position: 0 0; }
}

/* Indeterminate */
.vd-progress-indeterminate .vd-progress-bar {
  width: 30%;
  animation: progress-indeterminate 1.5s ease-in-out infinite;
}

@keyframes progress-indeterminate {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(400%); }
}

/* Reduced motion */
@media (prefers-reduced-motion: reduce) {
  .vd-progress-animated .vd-progress-bar,
  .vd-progress-indeterminate .vd-progress-bar {
    animation: none;
  }
}

/* Dark mode */
[data-theme="dark"] {
  --progress-bg: var(--bg-tertiary, #2d3748);
}

@media (prefers-color-scheme: dark) {
  :root:not([data-theme]) {
    --progress-bg: var(--bg-tertiary, #2d3748);
  }
}

/**
 * Vanduo Framework - Spinner Component
 * Loading indicators with variants and sizes
 */

:root {
  --spinner-size: 2rem;
  --spinner-border-width: 3px;
  --spinner-color: var(--color-primary);
  --spinner-speed: 0.65s;
}

/* Border Spinner */
.vd-spinner {
  display: inline-block;
  width: var(--spinner-size);
  height: var(--spinner-size);
  border: var(--spinner-border-width) solid var(--border-color, #e9ecef);
  border-top-color: var(--spinner-color);
  border-radius: 50%;
  animation: spinner-rotate var(--spinner-speed) linear infinite;
}

@keyframes spinner-rotate {
  to { transform: rotate(360deg); }
}

/* Dot Spinner */
.vd-spinner-dots {
  display: inline-flex;
  align-items: center;
  gap: 5px;
}

.vd-spinner-dots::before,
.vd-spinner-dots::after,
.vd-spinner-dots span {
  content: '';
  display: block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: var(--spinner-color);
  animation: spinner-dots-bounce 1.4s ease-in-out infinite both;
}

.vd-spinner-dots::before {
  animation-delay: -0.32s;
}

.vd-spinner-dots span {
  animation-delay: -0.16s;
}

@keyframes spinner-dots-bounce {
  0%, 80%, 100% { transform: scale(0); opacity: 0.5; }
  40% { transform: scale(1); opacity: 1; }
}

/* Growing Spinner */
.vd-spinner-grow {
  display: inline-block;
  width: var(--spinner-size);
  height: var(--spinner-size);
  background-color: var(--spinner-color);
  border-radius: 50%;
  opacity: 0;
  animation: spinner-grow 0.75s linear infinite;
}

@keyframes spinner-grow {
  0% { transform: scale(0); }
  50% { opacity: 1; }
}

/* Size Variants */
.vd-spinner-xs { --spinner-size: 1rem; --spinner-border-width: 2px; }
.vd-spinner-sm { --spinner-size: 1.5rem; --spinner-border-width: 2px; }
.vd-spinner-lg { --spinner-size: 3rem; --spinner-border-width: 4px; }
.vd-spinner-xl { --spinner-size: 4rem; --spinner-border-width: 5px; }

/* Color Variants */
.vd-spinner-success { --spinner-color: var(--color-success); }
.vd-spinner-warning { --spinner-color: var(--color-warning); }
.vd-spinner-error { --spinner-color: var(--color-error); }
.vd-spinner-info { --spinner-color: var(--color-info); }
.vd-spinner-light { --spinner-color: #fff; }
.vd-spinner-dark { --spinner-color: var(--text-primary); }

/* Centering Helper */
.vd-spinner-center {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: var(--spacing-5, 1rem);
}

/* Quad-Spinner Loader  signature Vanduo 2x2 grid loader */
.vd-dynamic-loader {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 1rem;
  gap: 1rem;
}

.vd-dynamic-loader-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.5rem;
}

.vd-dynamic-loader-text {
  color: var(--text-muted);
  font-size: var(--font-size-sm, 0.8125rem);
}

/* Quad-Spinner overlaying a skeleton card */
.vd-dynamic-loader-card {
  position: relative;
  min-height: 300px;
  padding: 2rem;
  overflow: hidden;
  border-radius: var(--btn-border-radius, 0.5rem);
  border: 1px solid var(--border-color);
}

.vd-dynamic-loader-card > .vd-dynamic-loader {
  position: absolute;
  inset: 0;
  padding: 0;
}

/* Reduced motion */
@media (prefers-reduced-motion: reduce) {
  .vd-spinner,
  .vd-spinner-dots::before,
  .vd-spinner-dots::after,
  .vd-spinner-dots span,
  .vd-spinner-grow {
    animation-duration: 1.5s;
  }
}

/**
 * Vanduo Framework - Button Group Component
 * Toolbar-style button grouping
 */

/* Button Group - Horizontal */
.vd-btn-group {
  display: inline-flex;
  position: relative;
  vertical-align: middle;
}

.vd-btn-group>.vd-btn {
  position: relative;
  flex: 0 1 auto;
  margin-bottom: 0;
}

/* Remove inner border-radius for adjacent buttons */
.vd-btn-group>.vd-btn:not(:first-child) {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  margin-left: -1px;
}

.vd-btn-group>.vd-btn:not(:last-child) {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

/* Ensure z-index for focus/active states */
.vd-btn-group>.vd-btn:hover,
.vd-btn-group>.vd-btn:focus,
.vd-btn-group>.vd-btn:active,
.vd-btn-group>.vd-btn.is-active {
  z-index: 1;
}

/* Button Group - Vertical */
.vd-btn-group-vertical {
  display: inline-flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: center;
}

.vd-btn-group-vertical>.vd-btn {
  width: 100%;
  margin-bottom: 0;
}

.vd-btn-group-vertical>.vd-btn:not(:first-child) {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  margin-top: -1px;
}

.vd-btn-group-vertical>.vd-btn:not(:last-child) {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

/* Sizing - inherit from button sizes */
.vd-btn-group-sm>.vd-btn {
  font-size: var(--font-size-sm);
  padding: 0.3125rem 0.625rem;
}

.vd-btn-group-lg>.vd-btn {
  font-size: var(--font-size-lg);
  padding: 0.625rem 1.25rem;
}

/* Full Width */
.vd-btn-group-full {
  display: flex;
  width: 100%;
}

.vd-btn-group-full>.vd-btn {
  flex: 1 1 auto;
}
/**
