  /* Search Results */
  --doc-search-results-bg: var(--card-bg, var(--bg-primary));
  --doc-search-results-border: var(--border-color);
  --doc-search-results-shadow: var(--shadow-lg);
  --doc-search-results-max-height: 400px;
