  /* Shortcut Badge */
  --doc-search-shortcut-bg: var(--bg-secondary);
  --doc-search-shortcut-border: var(--border-color);
  --doc-search-shortcut-color: var(--text-muted);
}

/* Dark Theme Overrides */
