/* Aspect Ratio Utilities */
.aspect-ratio {
  position: relative;
  width: 100%;
  padding-bottom: 56.25%; /* 16:9 default */
}

.aspect-ratio > * {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.aspect-ratio-1-1 {
  padding-bottom: 100%; /* 1:1 - Square */
}

.aspect-ratio-16-9 {
  padding-bottom: 56.25%; /* 16:9 - Video */
}

.aspect-ratio-4-3 {
