  :root:not([data-theme]) .table thead th {
    border-color: var(--table-border-color);
    color: var(--text-primary);
  }

  :root:not([data-theme]) .vd-table-striped tbody tr:nth-of-type(odd),
  :root:not([data-theme]) .table-striped tbody tr:nth-of-type(odd) {
    background-color: var(--table-striped-bg);
  }

  :root:not([data-theme]) .vd-table thead,
  :root:not([data-theme]) thead {
    background-color: var(--bg-secondary);
  }
}


/* Effects - Phase 7 */
/**
 * Vanduo Framework - Parallax Effects
 * Parallax scroll effects
 */

:root {
  /* Parallax Settings */
  --parallax-speed-slow: 0.5;
  --parallax-speed-medium: 1;
  --parallax-speed-fast: 1.5;
}

/* Base Parallax Container */
.vd-parallax {
  position: relative;
  overflow: hidden;
  width: 100%;
}

/* Parallax Layer */
.vd-parallax-layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  will-change: transform;
  backface-visibility: hidden;
  transform: translateZ(0);
}

/* Parallax Content */
.vd-parallax-content {
  position: relative;
  z-index: 1;
}

/* Parallax Speed Variants */
.vd-parallax-slow {
  --parallax-speed: var(--parallax-speed-slow);
}

.vd-parallax-medium {
  --parallax-speed: var(--parallax-speed-medium);
}

.vd-parallax-fast {
  --parallax-speed: var(--parallax-speed-fast);
}

/* Parallax Direction */
.vd-parallax-horizontal .vd-parallax-layer {
  /* Horizontal parallax will be handled by JS */
}

/* Parallax Image */
.vd-parallax-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  will-change: transform;
}

/* Parallax Background */
.vd-parallax-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 120%; /* Extra height for parallax effect */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  will-change: transform;
}

/* Parallax Container Heights (Fibonacci) */
.vd-parallax-sm {
  min-height: 377px; /* fib */
}

.vd-parallax-md {
  min-height: 610px; /* fib */
}

.vd-parallax-lg {
  min-height: 987px; /* fib */
}

.vd-parallax-full {
  min-height: 100vh;
}

/* Reduced Motion Support */
@media (prefers-reduced-motion: reduce) {
  .vd-parallax-layer,
  .vd-parallax-bg,
  .vd-parallax-image {
    transform: none !important;
    will-change: auto;
  }
}

/* Mobile Optimization (disable parallax on small screens) */
@media (max-width: 767.98px) {
  .vd-parallax-disable-mobile .vd-parallax-layer,
  .vd-parallax-disable-mobile .vd-parallax-bg,
  .vd-parallax-disable-mobile .vd-parallax-image {
    transform: none !important;
    will-change: auto;
  }
}



/* Print Styles */
/**
 * Vanduo Framework - Print Styles
 * Optimized styles for printing
 */

@media print {
  /* Reset backgrounds and shadows for printing */
  *,
  *::before,
  *::after {
    background: transparent !important;
    color: #000 !important;
    box-shadow: none !important;
    text-shadow: none !important;
  }

  /* Links */
  a,
  a:visited {
    text-decoration: underline;
  }

  a[href]::after {
    content: " (" attr(href) ")";
    font-size: 0.8em;
  }

  a[href^="#"]::after,
  a[href^="javascript:"]::after {
    content: "";
  }

  /* Images */
  img {
    max-width: 100% !important;
    page-break-inside: avoid;
  }

  /* Headings */
  h1, h2, h3, h4, h5, h6 {
    page-break-after: avoid;
    page-break-inside: avoid;
  }

  /* Paragraphs */
  p {
    orphans: 3;
    widows: 3;
  }

  /* Tables */
  table {
    border-collapse: collapse !important;
  }

  thead {
    display: table-header-group;
  }

  tr {
    page-break-inside: avoid;
  }

  /* Hide non-essential elements */
  .navbar,
  .navbar-menu,
  .navbar-toggle,
  .navbar-overlay,
  .sidenav,
  .modal,
  .modal-backdrop,
  .tooltip,
  .dropdown-menu,
  .btn-group,
  .pagination,
  .skip-link,
  .d-print-none {
    display: none !important;
  }

  /* Show print-only elements */
  .d-print-block {
    display: block !important;
  }

  .d-print-inline {
    display: inline !important;
  }

  .d-print-inline-block {
    display: inline-block !important;
  }

  /* Container adjustments */
  .container {
    width: 100% !important;
    max-width: none !important;
    padding: 0 !important;
  }

  /* Pre and code blocks */
  pre,
  blockquote {
    border: 1px solid #999;
    page-break-inside: avoid;
  }

  pre {
    white-space: pre-wrap !important;
  }
}

